@import "../helpers/variables";
// Project placeholders
//////////////////////////////////////////////////////
//////////////////////////////////////////////////////

%scroll-optimization {
  transform: translate3d(0, 0, 0);
  will-change: scroll-position;
}


%m-auto {
  margin: 0 auto;
}

%overlay {
  position: absolute;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  @include size(100%, 100%);
}

// Text overflow ellipsis protection
%string-ellipsis-protection {
  overflow: hidden;
  text-overflow: ellipsis;
  white-space: nowrap;
}

// Very long letters protection
%very-long-letters-protection {
  -ms-word-break: break-all;
  word-break: break-all;
  word-break: break-word; // stylelint-disable declaration-block-no-duplicate-properties
  -webkit-hyphens: auto;
  -moz-hyphens: auto;
  -ms-hyphens: auto;
  hyphens: auto;
}

